import React, { useState, useEffect } from "react";
import "./RubberDuck2.scss";

const RubberDuck2 = () => {
  const [pupilStyles, setPupilStyles] = useState({});
  const [coords, setCoords] = useState({
    x: 0,
    y: 0,
  });

  useEffect(() => {
    const onMouseMove = (e) => {
      const halfWindowHeight = window.innerHeight / 2;
      const halfWindowWidth = window.innerWidth / 2;

      if (e.pageX < halfWindowWidth) {
        // max is 10px to the left
        if (coords.x >= -10) {
          setCoords({
            x: coords.x - 0.25,
            y: coords.y,
          });
        }
      }

      if (e.pageX > halfWindowWidth) {
        // go right up to 6px from starting postiion
        if (coords.x <= 6) {
          setCoords({
            x: coords.x + 0.25,
            y: coords.y,
          });
        }
      }

      if (e.pageY < halfWindowHeight) {
        if (coords.y >= -5) {
          setCoords({
            x: coords.x,
            y: coords.y - 0.25,
          });
        }
      }

      if (e.pageY > halfWindowHeight) {
        if (coords.y <= 14) {
          setCoords({
            x: coords.x,
            y: coords.y + 0.25,
          });
        }
      }

      const styles = {
        transform: `translate(${coords.x}px, ${coords.y}px)`,
      };

      setPupilStyles(styles);
    };

    document.addEventListener("mousemove", onMouseMove);

    return () => {
      document.removeEventListener("mousemove", onMouseMove);
    };
  });

  return (
    <div className="rubber-duckie">
      <div className="rubber-duckie__container">
        <div className="rubber-duckie__duck">
          <div className="rubber-duckie__head" />

          <div className="rubber-duckie__face">
            <div className="rubber-duckie__eyes">
              <div className="rubber-duckie__eye-container">
                <div className="rubber-duckie__eye-outter" />
                <div className="rubber-duckie__eye-pupil" style={pupilStyles} />
              </div>
              <div className="rubber-duckie__eye-container">
                <div className="rubber-duckie__eye-outter" />
                <div className="rubber-duckie__eye-pupil" style={pupilStyles} />
              </div>
            </div>

            <div className="rubber-duckie__mouth-container">
              <div className="rubber-duckie__mouth-upper" />
              <div className="rubber-duckie__mouth-lower" />
            </div>
          </div>

          <div className="rubber-duckie__body" />
        </div>
      </div>
    </div>
  );
};

export default RubberDuck2;
